.container_Estudios{   
    width: 1130px;
    height: 585px;
    display: flex;
    border: 2.5px solid #20262d;
    background-image: url(../Img/A4.jpg);
    justify-content: space-between;
    color: black ;
    margin-top: 50px;
    margin-left: 110px;
}

.CuerpoTitle {
    margin-right: 96px;
}

.ContainerText {
    display: flex;
    flex-direction: column;
   /* width: 50%;*/
    margin: 95px;
    font-family: sans-serif;
    font-size: 15px;
    margin-top: 40px;
}

.ContainerText p{
    font-family: serif;
    font-size: 22px;
    margin: 5px;
    text-align: justify;
}

.ContainerImage{
    display: flex;
    width: 10%;
    height: 70%;
    margin-top: 48px;
    margin-left: 185px;
    justify-content: center;
}

.ContainerImage img {
    width: 160%;
    height: 55%;
    border-radius: 100%;
}

.Estudios {
   display: grid;
   position: absolute;
   width: 717px;
/*   height: 218;*/
   margin-top: 290px;
   margin-left: 15%;
}

 .Estudios button{
    width: 118px;
    height: 35px;
    color: black;
    margin: auto;
    background: rgb(228,100,100);
    background: linear-gradient(108deg, rgba(228,100,100,0.4430147058823529) 0%, rgba(90,161,184,0.5886729691876751) 50%, rgba(196,196,196,0.45702030812324934) 100%);
    border-radius:15px;
    font-size: 20px;
    }

.Estudios p{
    font-family: serif;
    margin-left: 15px;
    font-size: 28px;
    margin: 10px;
    text-align: justify;
}